.tox-tinymce {
  border: 1px solid #e4e6ef !important;
  border-radius: 0.375rem !important;
  box-shadow: none;
}
.tox-tinymce .tox-toolbar__primary {
  background-image: none !important;
  border-bottom: 1px solid #e4e6ef !important;
}
.tox-tinymce .tox-toolbar__primary .tox-tbtn .tox-tbtn__select-label {
  color: #6b7280 !important;
}
.tox-tinymce .tox-toolbar__primary .tox-tbtn svg {
  fill: #6b7280 !important;
}
.tox-tinymce .tox-toolbar__primary .tox-tbtn.tox-tbtn--enabled {
  background-color: transparent !important;
}
.tox-tinymce .tox-toolbar__primary .tox-tbtn.tox-tbtn--enabled svg {
  fill: #0284c7 !important;
}
.tox-tinymce .tox-toolbar__primary .tox-tbtn:hover {
  background-color: #f3f4f6 !important;
}

.tox-dialog-wrap__backdrop {
  background-color: rgba(0, 0, 0, 0.2) !important;
  -webkit-backdrop-filter: blur(4px) !important;
  backdrop-filter: blur(4px) !important;
}

.tox-dialog {
  border-radius: 0.5rem !important;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -4px rgba(0, 0, 0, 0.1) !important;
}
.tox-dialog .tox-dialog__header {
  padding: 1.2rem 1.6rem !important;
}
.tox-dialog .tox-dialog__header .tox-dialog__title {
  font-size: 1.15rem !important;
  line-height: 1.5 !important;
  font-weight: 500 !important;
}
.tox-dialog .tox-dialog__body-content {
  padding: 0 1.6rem !important;
}
.tox-dialog .tox-dialog__body-content .tox-form__group {
  margin-bottom: 0.75rem !important;
}
.tox-dialog .tox-dialog__body-content .tox-form__group label.tox-label {
  margin-bottom: 0.3rem !important;
  font-size: 1rem !important;
  color: #181c32 !important;
}
.tox-dialog .tox-dialog__body-content .tox-form__group .tox-textfield, .tox-dialog .tox-dialog__body-content .tox-form__group .tox-textarea {
  font-size: 1rem !important;
  font-weight: 400 !important;
  color: #181c32 !important;
  background-color: #fff !important;
  background-clip: padding-box !important;
  border: 1px solid #e4e6ef !important;
  appearance: none !important;
  border-radius: 0.375rem !important;
  line-height: 1.5rem !important;
  padding: 0.536rem 1rem !important;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out !important;
  box-shadow: none !important;
}
.tox-dialog .tox-dialog__body-content .tox-form__group .tox-textfield:focus, .tox-dialog .tox-dialog__body-content .tox-form__group .tox-textarea:focus {
  border-color: #7dd3fc !important;
  box-shadow: 0 0 0 1px #7dd3fc !important;
}
.tox-dialog .tox-dialog__footer {
  padding: 1.2rem 1.6rem !important;
  border: 0 !important;
}
.tox-dialog .tox-dialog__footer .tox-button {
  border-radius: 0.375rem !important;
  font-size: 1rem !important;
  font-weight: 500 !important;
  line-height: 1.666rem !important;
  padding: 0.536rem 1.65rem !important;
}